const ReceiptHeader = ({
    icon,
    title,
    date,
}) => {
    return <div>
        <Typography>{title}</Typography>
        <Typography>{date}</Typography>
    </div>;
}


const selectiveReceipt = (data) => {
    switch ((data.product || '').split('/').slice(1, 3).join('/')) {
        case 'pln/postpaid':
            return showPlnPostPaidReceipt(data);
        case 'pln/prepaid':
            return showPlnPrePaidReceipt(data);
        case 'asuransi/bpjskes':
            return showBPJSReceipt(data);
        case 'air/pdam':
            return showPdamReceipt(data);
        case 'pulsa/prepaid':
            return showMobilePrepaidReceipt(data);
        case 'pulsa/data':
            return showMobileDataReceipt(data);
        case 'kabel/transvision':
            return showTransvisionReceipt(data);
        case 'kabel/indovision':
            return showIndovisionReceipt(data);
        case 'pulsa/postpaid':
            return showMobilePostpaidReceipt(data);
        case 'finance/finansia':
            return showFinanceReceipt(data);
        case 'finance/bprkredit':
            return showFinanceReceipt(data);
        case 'finance/homecredit':
            return showFinanceReceipt(data);
        case 'finance/otocredit':
            return showFinanceReceipt(data);
        case 'transfer/dasawarsa':
            return showTransferReceipt(data);
        default:
            return '';
    }
}


const Page = () => {
    const user = useUserInfo();
    const id = getSearchParam().get('id');
    const list = useFetcher('/debit/history/' + id);
    return (
        <Defer data={user && list}>
            {() => <div>
                <Navbar />
                <List>
                    <ListItem>
                        <Breadcrumbs>
                            <Link color="inherit" to="/digi/history/">
                                Arsip Pembelian
                            </Link>
                            <Typography color="textPrimary">{list.title} #{id}</Typography>
                        </Breadcrumbs>
                    </ListItem>
                    <ListItem style={{flexDirection: 'column', alignItems: 'stretch'}}>
                        {selectiveReceipt(list)}
                    </ListItem>
                </List>
            </div>
            }
        </Defer>
    );
}

export default Page
